import React from "react";
import s from "./Post.module.css";

const Post = (props) => {
  const {likesCount} =props;
  return (
        <div className={s.postsItem}>
            <img src="data:image/jpeg;base64," />
            <div>
                {props.message}
            </div>
            <div>
                <span>like</span>{likesCount}
            </div>
        </div>
    );
};

export default Post;
